import React from 'react';
import $ from 'jquery';
import StartPageTwo from './StartPageTwo';


const handleButtonClick = function () {
  const windowHeight = $(window).height();
  window.scroll(0, windowHeight);
}


class Start extends React.Component {
  state = {
    addClassP: 'start ',
    addClassH1L: 'start left ',
    addClassH1R: 'start right ',
    addClassUl: ''
  }

  componentDidMount() {
    setTimeout(() => {
          this.setState({
            addClassP: 'start move',
            addClassH1L: 'start left move ',
            addClassH1R: 'start right move ',
            addClassUl: 'move'
          })

    }, 300);



  }
  render() {
    const { addClassUl,addClassP, addClassH1L, addClassH1R} = this.state;
    return (
      <>
<div className="frontPage">
  <div className="info" >
    <h1 className={addClassH1L}><span role="img" aria-label="men"></span> O mnie</h1>
    <p className={addClassP} >
      Nazywam si Karol, mam 20 lat. Programowania zaczem si uczy na pocztku kwietnia 2020 roku. Zaczem si tym pasjonowa. Staram si codziennie uczy czego nowego i rozwizywa napotkane mnie problemy. Jestem osob kreatywn i lubic pracowa w zespole. Z chci ucz si od innych w celu nabycia nowej wiedzy i nie popeniania tych samych bdw.
    </p>
  </div>
  <div className="info" >
    <h1 className={addClassH1R}><span role="img" aria-label="climbing"></span> Moje plany</h1>
    <p className={addClassP} >
      Moim planem na najblisze late jest dalszy rozwj. Chce si uczy coraz to nowszych technologii, nie opiera si na jednej. Zdecydowa czy do danego projektu jest lepiej uy React'a czy Angular'a. Mimo, e gwnie skupiam si na Front-Endzie, mam zamiar zacz przygod z backendem.<br/><span role="img" aria-label="run"></span> <strong>Chce biec cigle do przodu</strong>
    </p>
  </div>
  <div className="info" >
    <h1 className={addClassH1L}><span role="img" aria-label="programing"></span> Ukoczone kursy</h1>
    <p className={addClassP} >
      Poza szeroko dostpnymi dokumentacjami postanowiem zrobi kilka kursw w celu nabrania pewnych nawykw programistycznych. Ukoczone przeze mnie kursy byy ogldane na platformie Udemy.
      Prowadzcym by Bartomiej Borowczyk. Ukoczone kursy :<br/>
    </p>
      <ul className={addClassUl}>
        <li><span role="img" aria-label="lightning"> React od podstaw</span></li>
        <li><span role="img" aria-label="lightning"> Programowanie w JavaScript</span></li>
        <li><span role="img" aria-label="lightning"> Front-end zaawansowany w 15 dni</span></li>
        <li><span role="img" aria-label="lightning"> Wprowadzenie do Git i GitHub </span></li>
      </ul>
  </div>
  <div className="info" >
    <h1 className={addClassH1R}><span role="img" aria-label="programer"></span> Praca</h1>
    <p className={addClassP} >
      Tylko cik prac mona co osign. Wiem tyle, e pragn znale co co da mi moliwo ksztacenia mnie dalej. W pracy pragn si rozwija i pracowa w zespole. Wiem o tym, e swoj wytrwaoci jestem w stanie duo przej, eby sta si w tym wszystkim lepszy. Mam nadzieje, e ten czas ktry powicam na siebie zaowocuje
      <span role="img" aria-label="Strawberry"></span>
      <span role="img" aria-label="Grapes"></span>
      <span role="img" aria-label="Cherries"></span>
       i moje hobby stanie si moj prac.
    </p>
  </div>
      <button className="movePage" onClick={handleButtonClick}>
        <i className="fas fa-arrow-down"></i>
          </button>
          <div className="line"></div>
    </div>
      <StartPageTwo />
    </>
     );
  }
}



    export default Start;